import Vue from 'vue'
import axios from 'axios'
import qs from 'qs'
// axios 配置
axios.defaults.timeout = 10000;
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
 axios.defaults.baseURL = window.location.port == '8080' ? 'http://54.223.54.19:8088/' : ('/' + ( location.pathname.split('/')[1]));
//axios.defaults.baseURL = window.location.port == '8080' ? 'http://fit.oa.com/zxg/' : ('/' + ( location.pathname.split('/')[1]));
//axios.defaults.baseURL = 'http://fitdev.oa.com/zxg/'
export default{
  install: function (Vue) {
    // let baseURL = (window.location.port == '8080' ? 'http://10.10.82.75:8080/click-zxg/' : ('/' + (location.pathname.split('/')[1])));
    //成功回调函数
    let successAjaxHandle = (res, okCb, errCb, url) => {
      let result = res.data;
      if(typeof result == 'string'){
        okCb && okCb(result);
      }else if(typeof result == 'object'){
        if(!result.code){
          okCb && okCb(result);
        }else if(result.code == 200){
          okCb && okCb(result);
        }else{
          let msg = result.msg || '';
          // console.error(url + '接口调用失败，状态码：' + result.code + ' 错误消息：' + msg);
          errCb && errCb(result);//返回整个错误对象，以防接口code要做进一步的逻辑判断
        }
      }
    };

    let errorAjaxHandle = (res, errCb, url) => {
      if (res.response) {
        // 请求已发出，但服务器响应的状态码不在 2xx 范围内
        errCb && errCb(res.data);
      } else {
        // Something happened in setting up the request that triggered an Error
        console.error('Error URL：', url);
        console.error('Error', res);
      }
    };
    /**
     * 基于 axios 的ajax
     * @param {string} type 调用类型 'get' 'post'
     * @param {string} url 接口url
     * @param {object | null} params 接口参数
     * @param {Function} okCb 成功回调
     * @param {Function} errCb 失败回调
     * */
    let _ajax = (type, url, params, okCb, errCb) => {
      //url = baseURL + url;
      type = type.toLowerCase()
      if (type == 'post') {
        //params = qs.stringify(params);
        axios.post(url, params)
          .then(res => {
            successAjaxHandle(res, okCb, errCb, url);
          })
          .catch(res => {
            errorAjaxHandle(res, errCb, url);
          })
      } else {
        axios.get(url)
          .then(res => {
            successAjaxHandle(res, okCb, errCb, url);
          })
          .catch(res => {
            errorAjaxHandle(res, errCb, url);
          })
      }
    };

    Vue.prototype.$ajax = _ajax;
  }
}



