<template>
  <div class="hello">
    <el-form ref="form" :model="form" label-width="80px" >
      <el-row>
        <el-col :span="6">
          <div class="grid-content bg-purple">
          <el-form-item label="合同号">
            <el-input v-model="form.contractNum"></el-input>
          </el-form-item>
        </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-form-item label="文件名">
              <el-input v-model="form.contractName"></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-form-item label="目录名">
              <el-input v-model="form.directory"></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="6">
            <el-form-item label="资方" class="select-align">
              <el-select v-model="form.capital" placeholder="请选择资方">
                <el-option label="资方1" value="1"></el-option>
                <el-option label="资方2" value="2"></el-option>
                <el-option label="资方3" value="3"></el-option>
              </el-select>
            </el-form-item>
        </el-col>
      </el-row>
<el-row>

    <el-col :span="6">
      <el-form-item label="开始时间">
      <el-date-picker type="date" placeholder="选择日期" v-model="form.signDateStart" style="width: 100%;"></el-date-picker></el-form-item>
    </el-col>
  <el-col :span="6">
  <el-form-item label="结束时间">
      <el-date-picker type="date" placeholder="选择日期" v-model="form.signDateEnd" style="width: 100%;"></el-date-picker>
  </el-form-item>
  </el-col>
  <el-col :span="6">
    <div class="grid-content bg-purple">
      <el-form-item label=" 身份证号">
        <el-input v-model="form.identityCardNum"></el-input>
      </el-form-item>
    </div>
  </el-col>
  <el-col :span="6">
    <el-form-item label="合同状态" class="select-align">
      <el-select v-model="form.contractState" placeholder="请选择合同状态">
        <el-option label="初始化" value="1"></el-option>
        <el-option label="进行中" value="2"></el-option>
        <el-option label="已结清" value="3"></el-option>
      </el-select>
    </el-form-item>
  </el-col>
</el-row>

      <el-row>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-form-item label="客户姓名">
              <el-input v-model="form.clientName"></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-form-item label="手机号">
              <el-input v-model="form.clientMobile"></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-form-item label="客户编号">
              <el-input v-model="form.clientNum"></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content select-align">
          <el-form-item label="合同类型">
            <el-select v-model="form.contractStatus" placeholder="请选择合同类型">
              <el-option label="类型1""" value="1"></el-option>
              <el-option label="类型2" value="2"></el-option>
               <el-option label="类型2" value="2"></el-option>
            </el-select>
          </el-form-item></div>
        </el-col>


      </el-row>

      <el-form-item>
        <el-button @click="exportExcel">导出当前记录为excel</el-button>
        <el-button @click="exportContract">导出对应合同文件</el-button>
        <el-button @click="exportContractByExcel">根据导入excel，导出对应合同文件</el-button>
         <el-button  @click="getData"> 查询</el-button>
      </el-form-item>
      <el-table
        :data="tableData"
        border
        style="width: 100%;text-align: left">
        <el-table-column v-for="item in tableIndex"
                         v-bind:label="item.value"
                         v-bind:prop="item.key">
        </el-table-column>
      </el-table>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'Contract',
  data () {
    return {
     form: {
       contractId: "",
        contractNum: "",//合同号
        directory:"",//目录名
        clientMobile: "",
        clientNum: "",
        capital: "",//资方
        contractStatu: "",
        contractName: "",//文件名
        directory: "",
        signDate: "",
        identityCardNum: "",
        signDateStart:"", // 开始时间
        signDateEnd:"", // 结束时间
        identityCardNum:"",//
        clientName:""//客户姓名
     },
      tableIndex:[]
    }
  },
  methods:{
    getData(){
      let data = {

        contractId: this.contractId,
        contractNum: this.contractNum,
        clientMobile: this.clientMobile,
        clientNum: this.clientNum,
        clientName:this.clientName,
        capital: this.capital,
        contractStatus: this.contractStatus,
        contractName: this.contractName,
        directory: this.directory,
        signDateStart: this.signDateStart,
        signDateEnd: this.signDateEnd,
        identityCardNum: this.identityCardNum,
        s3Bucket: null,
        s3Key: null,
        operator: null,
        del: 0,
        pageIndex: 1,
        pageSize: 10
      }
      this.$ajax('post','http://54.223.54.19:8088/contract/search', data, res => {
        console.error(res)
        if (res != '' && res.data != undefined) {

          console.error('接口数据错误')
        }else{
          this.tableData =res.data
        }
      }, res => {
        console.log(res)

      })
    },
    exportExcel(){
    this.$ajax('post','contract/excel/export', data, res => {
        console.error(res)
        if (res != '' && res.data != undefined) {

          console.error('接口数据错误')
        }
      }, res => {
        console.log(res)

      })
    },
    exportContract(){
 this.$ajax('post','contract/zip', data, res => {
        console.error(res)
        if (res != '' && res.data != undefined) {

          console.error('接口数据错误')
        }
      }, res => {
        console.log(res)

      })
    },
    exportContractByExcel(){
       this.$ajax('post','contract/search', data, res => {
        console.error(res)
        if (res != '' && res.data != undefined) {

          console.error('接口数据错误')
        }
      }, res => {
        console.log(res)

      })
    }
  },
  mounted(){

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.select-align{
  text-align: left;
}


</style>



